import 'react-responsive-carousel/lib/styles/carousel.min.css'
import { Carousel as ResponsiveCarousel } from 'react-responsive-carousel'

export default function Carousel({
  work,
  activeImageIndex,
  setActiveImageIndex,
  setGalleryOpen,
}) {
  return (
    <div>
      <ResponsiveCarousel
        showThumbs={false}
        showArrows={true}
        showIndicators={false}
        infiniteLoop={true}
        showStatus={false}
        selectedItem={activeImageIndex}
        renderArrowPrev={(onClickHandler, hasPrev) =>
          hasPrev && (
            <div className="button left" onClick={onClickHandler}>
              <i className="icon-left-open"></i>
            </div>
          )
        }
        renderArrowNext={(onClickHandler, hasNext) =>
          hasNext && (
            <div className="button right" onClick={onClickHandler}>
              <i className="icon-right-open"></i>
            </div>
          )
        }
      >
        {work.map((project, index) => (
          <div key={project.image}>
            <img
              src={project.image}
              onClick={() => {
                setActiveImageIndex(index)
                // @ebaker - quick hack to remove double bars on small device height
                document.body.style.overflowY = 'hidden'
                setGalleryOpen(true)
              }}
              style={{ cursor: 'pointer' }}
            />

            <div className="legend" style={{ opacity: 0.9 }}>
              <h3>{project.title}</h3>
              <div>{project.subtitle}</div>
            </div>
          </div>
        ))}
      </ResponsiveCarousel>
      <div className="open-gallery-icon">
        <i className="icon-export"></i>
      </div>
    </div>
  )
}
