import { ReactBnbGallery } from 'react-bnb-gallery'
import 'react-bnb-gallery/dist/style.css'
import '../styles/gallery.styl'

export default function Carousel({
  work,
  activeImageIndex,
  setActiveImageIndex,
  galleryOpen,
  setGalleryOpen,
}) {
  return (
    <ReactBnbGallery
    show={galleryOpen}
    preloadSize={1}
    activePhotoIndex={activeImageIndex}
    photos={work.map(({ gallery }) => gallery)}
    onClose={() => {
      // @ebaker - quick hack to remove double bars on small device height
      document.body.style.removeProperty('overflow-y')
      setGalleryOpen(false)
    }}
    nextButtonPressed={() => {
      const nextIndex = activeImageIndex + 1 >= work.length ? 0 : activeImageIndex + 1
      setActiveImageIndex(nextIndex)
    }}
    prevButtonPressed={() => {
      const prevIndex =
        activeImageIndex - 1 < 0 ? work.length - 1 : activeImageIndex - 1
      setActiveImageIndex(prevIndex)
    }}
    activePhotoPressed={moveNext => {
      const link = work[activeImageIndex].gallery.link
      if (link) {
        window.open(link)
      } else {
        const nextIndex = activeImageIndex + 1 >= work.length ? 0 : activeImageIndex + 1
        setActiveImageIndex(nextIndex)
        moveNext()
      }
    }}
  />
  )
}
