import Head from 'next/head'
import { useState } from 'react'

import Carousel from '../components/Carousel'
import Gallery from '../components/Gallery'

import '../styles/app.styl'
import '../public/css/fontello.css'

import me from '../me.json'
const nav = ['contact', 'work', 'about']

export default function Folio() {
  const [active, setActive] = useState(nav[0])
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const [galleryOpen, setGalleryOpen] = useState(false)
  const [activeImageIndex, setActiveImageIndex] = useState(0)

  return (
    <div>
      <Head>
        <title>{me.title}</title>
        <link rel="icon" href="/favicon.svg" />
      </Head>

      <h1>{me.name}</h1>
      <div className="mobile">
        <div
          className="button"
          onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
        >
          <i className="icon-menu"></i>
        </div>
        <div className="name">{me.name}</div>
      </div>
      <div id="app">
        <div className="overlay"></div>
        <div className={`navigation ${mobileMenuOpen ? 'open' : ''}`}>
          <ul>
            <span className={`slider ${active}`}></span>
            {nav.map(view => (
              <li
                key={view}
                onClick={() => {
                  setActive(view)
                  setMobileMenuOpen(false)
                }}
                onMouseEnter={() => !mobileMenuOpen && setActive(view)}
              >
                {view}
              </li>
            ))}
          </ul>
        </div>
        <div className="main">
          <div className="mobile-subheader">{active}</div>
          <div className="circle"></div>
          <div className={`spinner ${active}`}>
            {nav.map(view => (
              <div
                key={view}
                className={`button ${view}`}
                onClick={() => setActive(view)}
              >
                <div className="info">
                  <div>{view}</div>
                </div>
              </div>
            ))}
          </div>
          <div className="wrapper">
            <div className="contents">
              <div
                className={`content contact ${
                  active === 'contact' && 'active'
                }`}
              >
                {me.contacts.map(contact => (
                  <div key={contact.icon}>
                    <a
                      className={contact.icon}
                      href={contact.url}
                      target="_blank"
                    >
                      <i className={`icon-${contact.icon}`}></i>
                      {contact.icon === 'file-pdf' && (
                        <div className="resume">Resume</div>
                      )}
                    </a>
                  </div>
                ))}
              </div>
              <div className={`content work ${active === 'work' && 'active'}`}>
                <Carousel
                  work={me.work}
                  activeImageIndex={activeImageIndex}
                  setActiveImageIndex={setActiveImageIndex}
                  setGalleryOpen={setGalleryOpen}
                />
              </div>
              <div
                className={`content about ${active === 'about' && 'active'}`}
              >
                <img src="/images/me.png" />
                <div>{me.about.bio}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Gallery
        work={me.work}
        activeImageIndex={activeImageIndex}
        setActiveImageIndex={setActiveImageIndex}
        galleryOpen={galleryOpen}
        setGalleryOpen={setGalleryOpen}
      />
    </div>
  )
}
